import { useContext, FormEvent } from 'react';
import { IsLoginContext } from '../contexts/IsLoginContext';

interface LoginProps {
  email: string;
  setEmail: (email: string) => void;
  password: string;
  setPassword: (password: string) => void;
}

const ParentLogin = ({
  email,
  setEmail,
  password,
  setPassword,
}: LoginProps) => {
  const { setIsLogin } = useContext(IsLoginContext);

  const handleLogin = (event: FormEvent) => {
    event.preventDefault(); //        
    // TODO: 1)    , 2)    token  localStorage .
    //   utils    ...? + setIsParent 
    setIsLogin(true);
  };

  return (
    <div>
      <form onSubmit={handleLogin}>
        <div>
          <label className="text-sm text-left text-black" htmlFor="email">
            email
          </label>
          <input
            type="email"
            id="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            className="mb-[10px] w-[352px] h-12 bg-white border border-[#c1c1c1]"
            required
          />
        </div>
        <div>
          <label className="text-sm text-left text-black" htmlFor="password">
            Password
          </label>
          <input
            type="password"
            id="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className="w-[352px] h-12 bg-white border border-[#c1c1c1]"
            required
          />
        </div>
        <button
          className="mt-[40px] w-[352px] h-16 bg-[#ffe090] text-sm text-center text-black"
          type="submit"
        >
           
        </button>
      </form>
    </div>
  );
};

export default ParentLogin;
